import { Component, Host, Prop, h } from '@stencil/core';

@Component({
  tag: 'mdx-carousel-image-placeholder',
  styleUrl: 'image-placeholder.scss',
  shadow: true,
})
export class CarouselImagePlaceholder {
  @Prop()
  src = '';

  @Prop()
  alt = 'Placeholder image';

  @Prop()
  slideNumber = 1;

  @Prop()
  width = '100%';

  @Prop()
  height = '19rem';

  render() {
    return (
      <Host
        style={{
          width: `min(${this.width}, calc(100% - var(--mdx-comp-carousel-spacing-carousel-box-content-gap-horizontal-cards)))`,
        }}
      >
        <div class="embla__slide">
          <div class="embla__slide__number">
            <span>{this.slideNumber}</span>
          </div>
          <img class="embla__slide__image" src={this.src} alt={this.alt} style={{ height: this.height }} />
        </div>
      </Host>
    );
  }
}
