:host .embla__slide {
  min-width: 0;
  position: relative;

  &__number {
    width: 3rem;
    height: 3rem;
    z-index: 1;
    position: absolute;
    top: 0.6rem;
    right: 0.6rem;
    border-radius: 50%;
    background-color: rgba(100, 100, 100, 0.85);
    line-height: 3rem;
    font-weight: 900;
    text-align: center;
    pointer-events: none;
  }

  &__image {
    display: block;
    width: 100%;
    object-fit: cover;
  }
}
